.cart-title {
	padding-top: 100px;

	.breadcrumb {
		display: flex;
		justify-content: center;
		align-items: center;
	
		.breadcrumb-arrow {
			margin: 0 10px;
			svg {
				width: 18px;
				color: #7d7d7d;
			}
		}
	
		li::before {
			content: '';
			padding: 0;
			margin: 0;
		}
	}
}
