/* Custom, iPhone Retina */
@media only screen and (min-width : 320px) and (max-width : 479px) {
	.mega-dropdown-menu{
		ul {
			list-style: none;
			padding: 0;

			li {
				a {
					line-height: 28px;
					padding: 0;

					i {
						margin-right: 8px;
					}
				}
			}
		 }
	}
}